import React, { useState } from "react";
import { Table } from "antd";

const columns = [
    {
        title: "H",
        dataIndex: "HoHocSinh",
    },
    {
        title: "Tn",
        dataIndex: "TenHocSinh",
        render: (text) => <a>{text}</a>,
    },
    {
        title: "Ngy sinh",
        dataIndex: "NgaySinh",
    },
    {
        title: "Gii tnh",
        dataIndex: "GioiTinh",
    },
    {
        title: "im 1",
        dataIndex: "Diem1",
    },
    {
        title: "im 2",
        dataIndex: "Diem2",
    },
    {
        title: "im 3",
        dataIndex: "Diem3",
    },
    {
        title: "im TB",
        dataIndex: "DiemTB",
    },
    {
        title: "Tnh trng",
        dataIndex: "TinhTrang",
    },
    {
        title: "",
        dataIndex: "",
        key: "x",
        render: () => (
            <div className="action-link">
                <a>Cp nht</a> | <a>Xa</a>
            </div>
        ),
    },
];

// rowSelection object indicates the need for row selection
const rowSelection = {
    onChange: (selectedRowKeys, selectedRows) => {
        console.log(
            `selectedRowKeys: ${selectedRowKeys}`,
            "selectedRows: ",
            selectedRows
        );
    },
    getCheckboxProps: (record) => ({
        disabled: record.name === "Disabled User",
        // Column configuration not to be checked
        name: record.name,
    }),
};
const TableStudent = ({ students }) => {
    return (
        <div className="table-student-container">
            <Table
                rowKey="id"
                rowSelection={{
                    type: "checkbox",
                    ...rowSelection,
                }}
                columns={columns}
                dataSource={students}
                size="small"
            />
        </div>
    );
};
export default TableStudent;
